.TodoListItem-virtualized{
  & + & {
    border-top: 1px solid gray;
  }
  &:nth-child(even){
    background: lightblue;
  }
}

.TodoListItem{
  padding: 1rem;
  display: flex;
  align-items: center;
  &:nth-child(even){
    background: #f8f9fa;
  }
  .checkbox{
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
    svg{
      font-size: 1.5rem;
    }
    .text{
      margin-left: 0.5rem;
      flex: 1;
    }
    &.checked{
      svg{color:#22b8cf;}
      .text{color: #adb5bd;
        text-decoration: line-through;
      }
    }
  }
  .remove{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: tomato;
    cursor: pointer;
    &:hover{
      color: red;
    }
  } 
}